@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
}

.last-update {
  background: rgb(187, 187, 187);
  border-radius: 12px;
  padding: 1px 8px;
  color: white;
  text-align: center;
}

table {
  width: 100%;
  border-collapse: collapse;
}

body {
  color: white;
  background: #242020;
}

.app {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}

td {
  text-align: center;
  padding: 15px;
  min-width: 130px;
  border-bottom: 3px solid #5f5e5e;
}

img {
  padding: 12px;
  width: 50px;
  height: auto;
}

td span {
  display: flex;
  align-items: center;
}

tr td:nth-child(2) {
  text-transform: uppercase;
  font-weight: bold;
}

.app-sub {
  margin: auto;
  text-align: center;
  display: flex;
  align-items: center;
}

.load {
  height: 100vh;
  justify-content: center;
}

.loading {
  font-size: 1.5rem;
  font-weight: bold;
}

@media only screen and (max-width: 768px) {

  .app {
    height: 100vh;
  }

  td {
    width: auto;
    padding: 21px 0;
    text-align: left;
  }

  td img {
    display: none;
  }

  tr td:nth-child(2) {
    display: none;
  }

  tr td:nth-child(3) {
    display: none;
  }

  tr td:nth-child(4) {
    display: none;
  }

  tr td:nth-child(6) {
    display: none;
  }

  tr td:nth-child(5) p {
    text-align: end;
  }
}

.load {
  display: flex;
  flex-direction: column;
  gap: 10px;
}


.loader {
  color: #ffffff;
  left: 30%;
  bottom: 20px;
  font-size: 45px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  transform: translateZ(0);
  animation: spintheball 1.7s infinite ease, round 1.7s infinite ease;
}

@keyframes spintheball {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em,
      0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }

  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em,
      0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,
      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }

  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em,
      -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em,
      -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
  }

  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em,
      -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em,
      -0.749em -0.34em 0 -0.477em;
  }

  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em,
      -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em,
      -0.82em -0.09em 0 -0.477em;
  }

  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em,
      0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
}

@keyframes round {
  0% {
    transform: rotate(0deg)
  }

  100% {
    transform: rotate(360deg)
  }
}